*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  position: relative;
  width: 100%;
  height: 100vh;
}
.center_div{
  width: 400px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  padding: 50px;
  background: rgba(0,0,0,0.8);
  border-radius: 10px;
}
.center_div  h3{
  margin-bottom: 30px;
  color: #fff;
  text-align: center;
  text-transform: capitalize;
}
.center_div .inputbox{
  position: relative;
}
.center_div .inputbox input{
  width: 100%;
  padding: 10px;
  font-size: 16px;
  color: #fff;
  letter-spacing: 1px;
  margin-bottom: 30px;
  border: none;
  border-bottom: 1px solid #fff;
  background: transparent;
  outline: none;
}
